@media (max-width: 648px) {
  /*Mobile View */
  .header {
    padding: 0px 10px;
  }
  .header__logo {
    font-size: 35px;
  }
  .nav-options {
    display: flex;
    width: 100%;
    height: 100px;
    position: absolute;
    top: 40px;
    left: -100%;
    opacity: 0;
/*     transition: all 0.5s ease;*/    
    flex-direction: column;
    list-style-type: none;
    grid-gap: 15px;
  }
  .nav-options.active {
    background: white;
    left: 0;
    opacity: 1;
    z-index: 1;
    align-content: center;
    padding: 10px;
  }

  .menu-icon {
    width: 30px;
    height: 30px;
  }

  .mobile-menu {
    display: block;
  }
}


.App {
    font-family: Arial, Helvetica, sans-serif;
    margin: auto;
    text-align: center;
    vertical-align: baseline;
    position: relative;
    min-height: 100vh;
    padding-bottom: 2.5rem;
}
.productCard {
    margin-bottom: 40px;
    width: 270px;
    margin: 20px;

}

.productCard > img {
    height: 300px;
    width: 300px;
    object-fit: cover;
    border-color: rgba(255, 255, 255, 0.911);
    border-radius: 10px;
    border-width: 50px;
}
.products {
    display: flex;
    flex-wrap: wrap;
    padding-inline: 200px;
    padding-block: 50px;
    margin: auto;
}


.cover {
    width: 80%;
    height: auto;
    object-fit: contain;
    display: flex;
    text-align: center;
}

.cover_div {
    max-width: 1000px;
    height: auto;
    margin: auto;
}

