.order {
    display: inline-flexbox;
    align-items: center;
    padding-inline: 200px;
    padding-top: 50px;
}
.about {
    display: inline-flexbox;
    font-style: italic;
    align-items: center;
    padding-inline: 200px;
    padding-top: 50px;
    height: 100%;
}
.footer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 2.5rem;
    background-color: rgba(35, 82, 35, 0.219);
    position: absolute;
    bottom:0;
}

.footer > p {
